(function togglerScript($, Drupal) {
  Drupal.behaviors.toggler = {
    attach(context) {
      $('.toggleable').hide();
      $('.toggle').click(function(){
        $('.toggleable').slideToggle( "slow", function() {
          $('.toggle').toggleClass('open', $(this).is(':visible'));

        });
      });
    },
  };
}(jQuery, Drupal));
